.btn-fixed {
	font-family: 'PFDinDisplayPro-Bold', sans-serif;
	color: #fff;
	font-size: 16px;
	text-decoration: none;
	text-transform: uppercase;
	width: 59px;
	height: 193px;
	background: #555555;
	border-radius: 2px 2px 0 0;
	box-shadow: 0px 25px 49px 1px rgba(15,17,19,.16);
	position: fixed;
	top: 286px;
	right: 0;
	z-index: 1;
	// padding: 24px 0 0 24px

	@include r(1400) {
		width: 50px;
		height: 180px;
		background: rgba(85,85,85,0.5); }

	&__text {
		position: absolute;
		transform: rotate(-90deg);
		width: 148px;
		top: 86px;
		right: -49px;

		@include r(1400) {
			top: 79px;
			right: -51px; } } }

.description {
	background-image: url('../img/description-bg.jpg');
	background-position: -10px -50px;
	/*height: 100vh*/
	height: 972px;

	@include r(985) {
		height: 100%; }

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center; }

	&__title {
		font-family: 'PFDinDisplayPro-Regular', sans-serif;
		color: #555555;
		font-size: 96px;
		text-transform: uppercase;
		margin-top: 100px;
		letter-spacing: 0.032em;

		@include r(985) {
			font-size: 75px;
			margin-top: 50px; }

		@include r(730) {
			font-size: 50px; }

		@include r(490) {
			font-size: 32px; } }

	&__description {
		font-family: 'PFDinDisplayPro-Medium', sans-serif;
		color: #555555;
		font-size: 20px;
		margin: 40px 195px 0 326px;
		letter-spacing: 0.024em;
		line-height: 1.5em;
		position: relative;

		@include r(1125) {
			margin: 40px 60px 0 326px; }

		@include r(985) {
			font-size: 15px; }

		@include r(830) {
			margin: 15px 60px 0 200px; }

		@include r(730) {
			font-size: 13px; }

		@include r(645) {
			margin: 15px 5px 0 125px; }

		@include r(520) {
			font-size: 12px;
			margin: 15px 5px 0 75px; }

		@include r(490) {
			margin: 15px 5px 0 33px; }

		&:before {
			position: absolute;
			content: '';
			top: 14px;
			left: -90px;
			width: 71px;
			height: 2px;
			background: #555555;

			@include r(985) {
				top: 10px; }

			@include r(730) {
				top: 9px;
				left: -40px;
				width: 35px;
				height: 1px; }

			@include r(490) {
				left: -25px;
				width: 20px; } } }

	&__link {
		font-family: 'PFDinDisplayPro-Bold', sans-serif;
		color: #a7d2db;
		font-size: 13px;
		text-transform: uppercase;
		margin: 1px 223px 43px 0;
		letter-spacing: 0.2em;
		align-self: flex-end;
		position: relative;

		@include r(1125) {
			margin: 15px 80px 25px 0; }

		@include r(490) {
			margin: 15px 20px 25px 0; }

		&:hover {
			text-decoration: none; } }

	&__triangle {
		position: absolute;
		width: 0;
		height: 0;
		border-top: 4px solid transparent;
		border-left: 5px solid #a7d2db;
		border-bottom: 4px solid transparent;
		top: 4px;
		right: -13px; }

	&__text {
		font-family: 'PFDinDisplayPro-Thin', sans-serif;
		color: #555555;
		font-size: 20px;
		margin: 0 92px 30px 78px;
		letter-spacing: 0.025em;
		line-height: 1.5em;

		@include r(885) {
			margin: 0 65px 15px 15px; }

		@include r(730) {
			font-size: 16px;
			margin: 0 15px 15px 15px;
			text-align: justify; }

		@include r(425) {
			margin: 0 5px 5px 5px; } } }

.nav {
	&__top {
		display: flex;
		justify-content: flex-end; } }

.menu {
