.steps {
	height: 400px;
	background: #a7d2db;

	@include r(730) {
		height: auto; }

	&__content {
		display: flex;
		padding: 128px 0 0 41px;

		@include r(1260) {
			padding: 0 215px;
			flex-wrap: wrap;
			justify-content: space-evenly;
			align-items: center;
			height: 400px; }

		@include r(1135) {
			padding: 0 155px; }

		@include r(1000) {
			padding: 0 85px; }

		@include r(875) {
			padding: 0 23px; }

		@include r(730) {
