	&__item {
		/*padding: 0 19px 0 41px*/
		width: 277px;
		position: relative;

		&:nth-child(2) {
			margin-left: 30px;

			@include r(1260) {
				margin-left: 0; } }

		&:nth-child(4) {
			margin-left: 82px;

			@include r(1260) {
				margin-left: 0; } }

		&:after {
			position: absolute;
			content: '';
			top: 63px;
			left: -15px;
			background: #fff;
			width: 1px;
			height: 63px; } }

	&__title {
		font-family: 'PFDinDisplayPro-Bold', sans-serif;
