		transform: rotate(-90deg);
		top: -5px;
		left: -50px; } }

.schedule {
	background-image: url('../img/schedule-bg.jpg');
	background-position: -10px -4px;
	height: 1599px;

	@include r(1260) {
		height: auto;
		padding-bottom: 25px; }

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 79px 0 0 0; }

	&__title-s {
		font-family: 'PFDinDisplayPro-ExtraThin', sans-serif;
		color: #555555;
		font-size: 36px;
		font-weight: 100;
		text-transform: uppercase;
		letter-spacing: 0.6em;
		margin-left: 17px;
		position: relative;

		@include r(666) {
			font-size: 30px; }

		@include r(560) {
			font-size: 25px; }

		@include r(470) {
			font-size: 17px;
