			left: 314px;
			background: #828384;
			width: 1px;
			height: 30px;

			@include r(666) {
				top: 40px;
				left: 50%; }

			@include r(470) {
				top: 25px;
				height: 25px; } } }

	&__title-l {
		font-family: 'PFDinDisplayPro-Regular', sans-serif;
		color: #555555;
		font-size: 59px;
		text-transform: uppercase;
		margin: 54px -4px 0 0;
		letter-spacing: 0.03em;
		position: relative;

		@include r(470) {
			font-size: 48px;
			margin: 27px 0 0 0; }

		&:after {
			position: absolute;
			content: '';
			width: 0;
			height: 0;
			border-left: 16px solid transparent;
			border-right: 15px solid transparent;
			border-top: 18px solid #a7d2db;
			top: 82px;
			left: 172px;

			@include r(666) {
				left: calc(50% - 8px); }

			@include r(470) {
				left: calc(50% - 12px);
				top: 60px; } } } }

.table {
	align-self: flex-start;
	margin: 150px 0 0 42px;
	width: 1215px;

